import React from 'react';
import {BaseHeaderProps, Header, HeaderTag, useBreakpoint} from '@spinglobal/react-foundations';
import {
    style__font__HeaderPrimary,
    style__font__HeaderPrimary_Upper,
    style__font__HeaderPrimary_White
} from '../../../styles';

export function HeaderPrimary({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    const bp = useBreakpoint();
    return <Header level={HeaderTag.Heading1} style={{...style__font__HeaderPrimary(bp), ...style}} classes={classes} {...headerProps}/>
}

export function HeaderPrimaryWhite({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    const bp = useBreakpoint();
    return <Header level={HeaderTag.Heading1} style={{...style__font__HeaderPrimary_White(bp), ...style}} classes={classes} {...headerProps}/>
}

export function HeaderPrimaryUpper({style = {}, classes = [], ...headerProps}: BaseHeaderProps) {
    const bp = useBreakpoint();
    return <Header level={HeaderTag.Heading1} style={{...style__font__HeaderPrimary_Upper(bp), ...style}} classes={classes} {...headerProps}/>
}
