import React from 'react';
import {
    BordererPresets,
    Flexer,
    FlexerPresets,
    FlexWrap,
    Fonter,
    MarginerPresets,
    Shadower,
    shiftBreakpoint,
    Surface,
    TextAlign,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {HeaderPrimary, Page, TextPrimary} from '../../Components';
import ClientsList from './clientsList';

const paragraph1 = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor in';

export function Clients() {
    const bp = useBreakpoint();
    return <Page headerText='Our Clientele' text={paragraph1}>
        <Surface style={{
            ...FlexerPresets.MainCenter, ...Flexer({wrap: FlexWrap.Wrap}),
            maxWidth: shiftBreakpoint(bp, -1), ...MarginerPresets.auto
        }}>
            {ClientsList.map(e => {
                return <Surface key={e.name} style={{
                    backgroundImage: `url(${e.src})`,
                    backgroundSize: 'cover',
                    width: '10vw',
                    height: '10vw',
                    minWidth: 96,
                    minHeight: 96,
                    ...MarginerPresets._2,
                    ...BordererPresets.P50,
                    ...Shadower({voffset: 2, hoffset: 2, blur: 4})
                }}/>
            })}
        </Surface>
    </Page>;
}
