export function TopBar() {
    const bp = useBreakpoint();
    const [open, setOpen] = useState<boolean>(false);

    const clickHandler = (e: SyntheticEvent) => {
        e.stopPropagation();
        setOpen(true);
    }
    const barFlex = bp <= BreakpointMedium ? FlexerPresets.ColumnCenter : FlexerPresets.Flex;
    let linkSize = '2vw';
    if (bp <= BreakpointSmall) {
        linkSize = '4vw';
    } else if (bp <= BreakpointMedium) {
        linkSize = '3vw';
    }

    let linkHeight: string | number = '';
    if (bp <= BreakpointSmall && !open) {
        linkHeight = 0;
    }

    useEffect(() => {
        const body = document.getElementsByTagName('body')[0];
        body.addEventListener('click', (e) => {
            e.stopPropagation();
            setOpen(false)
        }, false);
    }, []);

    const linkFlex = bp <= BreakpointSmall ? FlexerPresets.ColumnCenter : Flexer({
        main: FlexMainAxis.SpaceAround,
        secondary: FlexSecondaryAxis.Center
    });
    return <>
        <PositionBlock position={PositionTypes.Sticky} top={0} left={0}
                       style={{zIndex: 9998, background: ColorSet.Primary3}}>
            <Surface style={{...barFlex, ...ShadowerPresets.Blur16, ...PaddingerPresets._1}}>
                <Surface style={{background: ColorSet.Primary3, position: 'relative'}}>
                    <a style={{textDecoration: 'none'}} href='#'><Image src='./spin_logo.png' alt='SPIN Logo' style={{
                        width: '100%',
                        paddingTop: 4,
                        minWidth: 90,
                        maxWidth: 200
                    }}/></a>
                </Surface>
                <Surface style={{width: '90vw', ...linkFlex, height: linkHeight, overflow: 'hidden'}}>
                    <a style={{textDecoration: 'none'}} href='#mission'><HeaderSecondary text='Mission'
                                                                                         style={Fonter({fontSize: linkSize})}/></a>
                    <a style={{textDecoration: 'none'}} href='#our_solutions'><HeaderSecondary text='Solutions'
                                                                                               style={Fonter({fontSize: linkSize})}/></a>
                    <a style={{textDecoration: 'none'}} href='#our_platforms'><HeaderSecondary text='Platforms'
                                                                                               style={Fonter({fontSize: linkSize})}/></a>
                    <a style={{textDecoration: 'none'}} href='#our_team'><HeaderSecondary text='Team'
                                                                                          style={Fonter({fontSize: linkSize})}/></a>
                    <a style={{textDecoration: 'none'}} href='#our_work'><HeaderSecondary text='Work'
                                                                                          style={Fonter({fontSize: linkSize})}/></a>
                    <a style={{textDecoration: 'none'}} href='#our_clientele'><HeaderSecondary text='Clientele'
                                                                                               style={Fonter({fontSize: linkSize})}/></a>
                </Surface>
            </Surface>
            {bp <= BreakpointSmall && !open ? <PositionBlock position={PositionTypes.Absolute} top={50} right={50} style={{zIndex: 10001}}>
                <button onClick={clickHandler}>Open</button>
            </PositionBlock> : <></>}
        </PositionBlock>
    </>;
}
