import React from 'react';
import {
    Flexer,
    FlexerPresets,
    FlexWrap,
    MarginerPresets,
    shiftBreakpoint,
    Surface,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {MemberCard, MemberCardProps, Page} from '../../Components';
import TeamMembers from './teamMembers';

function sorter(a: MemberCardProps, b: MemberCardProps) {
    if (a.last_name < b.last_name) {
        return -1;
    }
    if (a.last_name > b.last_name) {
        return 1;
    }
    return 0;

}

const teamSubtext = 'We are compassionate people working together for a higher purpose, seeking to create value for clients and humanity with a spirit of excellence.';

export function Team() {
    const bp = useBreakpoint();
    return <Page headerText='Our Team' text={teamSubtext}>
        <Surface style={{
            ...FlexerPresets.MainCenter, ...Flexer({wrap: FlexWrap.Wrap}),
            maxWidth: shiftBreakpoint(bp, -1), ...MarginerPresets.auto
        }}>
            {TeamMembers.sort(sorter).map(e => <MemberCard key={e.last_name + e.title} {...e}/>)}
        </Surface>
    </Page>
}
