import React from 'react';
import {
    BordererPresets,
    BreakpointMedium,
    Fonter,
    FontStyle, MarginerPresets, Paddinger, PaddingerPresets,
    Surface,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {HeaderSecondary} from '../Headers';
import {TextPrimaryCenter} from '../Text';
import {ColorSet} from '../../styles';

export interface TestimonialProps {
    text: string;
    name: string;
    title: string;
}

export function Testimonial({...props}: TestimonialProps) {
    const {text, name, title} = props;
    const width = useBreakpoint() <= BreakpointMedium ? '90%' : '65%';
    return <Surface style={{overflow: 'auto', width: '100%', background: ColorSet.Primary2}}>
        <Surface style={{
            ...MarginerPresets.auto_4,
            ...BordererPresets._8,
            width,
            background: ColorSet.Primary3
        }}>
            <TextPrimaryCenter
                text={`"${text}"`}
                style={{
                    ...Paddinger({top: 5, left: 5, right: 5, bottom: 0}),
                    ...Fonter({fontStyle: FontStyle.Italic})
                }}/>
            <Surface style={PaddingerPresets._2}>
                <HeaderSecondary text={name}/>
                {title ? <TextPrimaryCenter text={title}/> : <></>}
            </Surface>
        </Surface>
    </Surface>;
}
