import React, {PropsWithChildren} from 'react';
import {FlexerPresets, MarginerPresets, shiftBreakpoint, Surface, useBreakpoint} from "@spinglobal/react-foundations";
import {HeaderPrimary} from "../Headers";
import {TextPrimaryCenter} from "../Text";

export function Page({children, headerText, text}: PropsWithChildren<{ headerText: string, text: string }>) {
    const bp = useBreakpoint();
    return <Surface style={FlexerPresets.Center}>
        <div style={{position: 'relative', top: '-13vh'}} id={headerText.toLowerCase().replace(/ /g, '_')}/>
        <HeaderPrimary
            text={headerText}
            style={MarginerPresets._3}
        />
        <TextPrimaryCenter
            text={text}
            style={{
                maxWidth: shiftBreakpoint(bp, -2),
            }}
        />
        {children}
    </Surface>
}
