import React from 'react';
import {Page} from "../../Components/Page";
import {
    BordererPresets,
    FlexerPresets,
    Fonter, Hover, Marginer,
    MarginerPresets, PaddingerPresets, Shadower,
    ShadowerPresets,
    Surface
} from "@spinglobal/react-foundations";
import {HeaderSecondary} from "../../Components/Headers/HeaderSecondary";
import {TextPrimaryCenter} from "../../Components/Text/TextPrimary";

function CapabilityParagraph({text}: { text: string }) {
    return <TextPrimaryCenter text={text} style={{
        padding: '0 18px',
        display: 'inline',
        ...Fonter({level: 4})
    }}/>
}

function CapabilitySection(obj: { text: string }) {
    const {text} = obj;
    return <Surface style={{...MarginerPresets._3, ...ShadowerPresets.Blur8, ...BordererPresets._8, ...PaddingerPresets._4}}>
        <HeaderSecondary text={text.slice(0, 1)} style={{marginRight: -18, display: 'inline', ...Fonter({level: 6})}}/>
        <CapabilityParagraph text={text.slice(1)}/>
        <Hover hoverStyles={ShadowerPresets.Blur2}>
            <Surface style={{
                ...BordererPresets._4,
                ...Shadower({hoffset: 1, voffset: 1, blur: 4}),
                ...FlexerPresets.Center,
                ...MarginerPresets.auto,
                ...Marginer({top: 2})
            }}>
                <TextPrimaryCenter
                    text='Learn More'
                    style={{...MarginerPresets._0, ...PaddingerPresets._N3}}
                />
            </Surface>
        </Hover>
    </Surface>
}

const capabilities = [
    'Data-driven assessments before, during and after disasters',
    'Institutional capacity building and regional security cooperation',
    'Strategic management, scientific, and technical consulting',
    'Rapid response and recovery support',
    'Universal risk reduction, resilience and emergency management services',
    'Planning, training and exercises for all hazards and incident types',
    'Teach and prepare neighborhoods and nations using our "Planet Ready" platform'
]

export function Capabilities() {
    return <Page headerText='Our Solutions' text='At vero eos et accusamus et iusto odio dignissimos ducimus qui blanditiis praesentium voluptatum deleniti atque corrupti quos dolores et quas molestias excepturi'>
        <Surface style={FlexerPresets.ColumnCenter}>
            <Surface>
                {capabilities.map(e => <CapabilitySection key={e} text={e}/>)}
            </Surface>
        </Surface>
    </Page>;
}
