import React from 'react';
import {Page, TextPrimaryWhite, HeaderTertiary} from '../../Components';
import {
    BordererPresets,
    BreakpointLarge,
    BreakpointMedium,
    BreakpointSmall,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    FlexSecondaryAxis,
    FlexWrap,
    Hover,
    Image,
    Marginer,
    MarginerPresets,
    Shadower,
    Surface,
    useBreakpoint
} from '@spinglobal/react-foundations';
import {ColorSet} from '../../styles';

export function PlatformLogo({icon}: { icon: string }) {
    return <Image src={icon} alt='Icon'/>;
}

export interface PlatformCardProps {
    icon: string;
    title: string;
}

export function PlatformCard({icon, title}: PlatformCardProps) {
    const width = useBreakpoint() <= BreakpointSmall ? '100%' : 520;
    const height = 320;
    const margin = useBreakpoint() <= BreakpointSmall ? Marginer({top: 4, bottom: 4}) : MarginerPresets._4;

    return <Surface style={{
        width,
        height,
        background: ColorSet.Primary2,
        ...margin,
        ...Shadower({voffset: 30, blur: 90}),
        ...FlexerPresets.Center
    }}>
        <PlatformLogo icon={icon}/>
        <HeaderTertiary
            text={title}
            style={MarginerPresets._3}
        />
        <Hover hoverStyles={{backgroundColor: ColorSet.Secondary3}}>
            <Surface style={{
                ...BordererPresets._4,
                ...Shadower({hoffset: 1, voffset: 1, blur: 2}),
                ...FlexerPresets.Center
            }}>
                <TextPrimaryWhite
                    text='Visit'
                    style={MarginerPresets._0}
                />
            </Surface>
        </Hover>
    </Surface>;
}

export function Platforms() {
    const bp = useBreakpoint();
    const direction = bp <= BreakpointMedium ? FlexDirection.Column : FlexDirection.Row;
    const paragraph1 = 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.';
    return <Page headerText='Our Platforms' text={paragraph1}>
        <Surface style={{
            maxWidth: BreakpointLarge,
            ...Flexer({
                direction,
                main: FlexMainAxis.SpaceAround,
                secondary: FlexSecondaryAxis.Center,
                wrap: FlexWrap.Wrap
            })
        }}>
            <a href='https://www.planetready.com' style={{textDecoration: 'none'}}><PlatformCard
                icon='https://planet-ready-combined-app.s3.amazonaws.com/assets/planetready-logo-300x80.png'
                title={'Planet Ready'}/></a>
            <a href='https://www.respondercq.com' style={{textDecoration: 'none'}}><PlatformCard
                icon='https://respondercq.com/assets/logo-350x60.jpg' title={'ResponderCQ'}/></a>
        </Surface>
    </Page>;
}
