import React, {CSSProperties, PropsWithChildren} from 'react';
import {HeaderSecondary, Page, TextPrimaryCenter} from '../../Components';
import CONTENT from './missionStatements';
import {
    BordererPresets,
    BreakpointLarge,
    FlexDirection,
    Flexer,
    FlexerPresets,
    FlexMainAxis,
    Image,
    Shadower,
    Surface,
    useBreakpoint, Marginer, PaddingerPresets, MarginerPresets
} from '@spinglobal/react-foundations';

function MissionParagraph({text}: { text: string }) {
    return <TextPrimaryCenter text={text} style={{
        padding: '0 18px',
        display: 'inline',
    }}/>
}

function MissionImage() {
    const width = useBreakpoint() <= BreakpointLarge ? '80%' : '45vw';
    return <Surface style={{...MarginerPresets.auto, alignSelf: 'start', width, maxWidth: 800}}>
        <Image
            src='https://media.istockphoto.com/photos/team-teamwork-business-join-hand-together-concept-power-of-volunteer-picture-id1017183652?s=2048x2048'
            alt='Recovering From Disaster'
            style={{
                maxWidth: 800,
                width,
                display: 'block',
                userSelect: 'none',
                ...MarginerPresets.auto_0,
                ...BordererPresets._8,
                ...Shadower({
                    voffset: 4,
                    spread: 4,
                    blur: 8,
                    color: 'rgba(0, 0, 0, .25)'
                })
            }}/>
    </Surface>
}

function MissionRow({children, style}: PropsWithChildren<{ style?: CSSProperties }>) {
    const flexer = useBreakpoint() <= BreakpointLarge ? FlexerPresets.ColumnCenter : FlexerPresets.MainCenter;
    return <Surface style={{...style, ...PaddingerPresets._0, ...flexer}}>
        {children}
    </Surface>;
}

function MissionSection(obj: { leadin: string, text: string }) {
    const {leadin, text} = obj;
    return <Surface style={{textAlign: 'center', ...MarginerPresets._3}}>
        <HeaderSecondary text={leadin} style={{marginRight: -14, display: 'inline'}}/>
        <MissionParagraph text={text}/>
    </Surface>
}

export function Mission() {
    return <Page headerText='Our Mission' text=''>
        <MissionRow style={Marginer({right: 3})}>
            <Surface style={Flexer({direction: FlexDirection.Column, main: FlexMainAxis.SpaceAround})}>
                {CONTENT[0].map(e => <MissionSection key={e.leadin} {...e}/>)}
            </Surface>
            <MissionImage/>
        </MissionRow>
        <MissionRow>
            {CONTENT[1].map(e => <MissionSection key={e.leadin} {...e}/>)}
        </MissionRow>
    </Page>;
}
