import React, {useState} from 'react';
import {
    Action,
    BordererPresets,
    ComponentProps,
    FlexerPresets,
    Image,
    Marginer,
    MarginerPresets,
    PaddingerPresets,
    Shadower,
    ShadowerPresets,
    Surface, TextAlign,
    useBreakpoint,
    Modal
} from '@spinglobal/react-foundations';

import {ColorSet, style__font__TextPrimary} from '../../styles';
import {HeaderSecondary} from '../Headers/HeaderSecondary';
import {TextPrimary, TextPrimaryCenter} from '../Text/TextPrimary';

export interface MemberCardProps extends ComponentProps {
    src: string;
    first_name: string;
    last_name: string;
    title: string;
    bio: string;
    education: Array<string>;
    experience: string;
    ask: Array<string>;
    ln?: string;
}

function MemberBulletSection({data, heading, num}: { data: Array<string>, heading: string, num: number }) {
    const paddingLeft = 24;
    const marginTop = num !== 0 ? 12 : 0;
    const bp = useBreakpoint();
    return <Surface>
        <HeaderSecondary text={heading} style={{marginTop, textAlign: TextAlign.Left}}/>
        <ul style={{paddingLeft}}>
            {data.map(e => <li key={e}><Surface style={style__font__TextPrimary(bp)}>{e}</Surface></li>)}
        </ul>
    </Surface>;
}

export function MemberCard({src, first_name, last_name, title, bio, experience, ask, education, ln}: MemberCardProps) {
    const [open, setOpen] = useState<boolean>(false);
    return <>
        <Modal style={{background: 'rgba(0, 0, 0, .8)'}} open={open} onClickCapture={() => setOpen(false)}>
            <Surface style={{
                background: ColorSet.Primary3,
                maxWidth: '80vw', ...PaddingerPresets._6,
                overflow: 'auto', ...MarginerPresets._6,
                ...BordererPresets._8,
                ...ShadowerPresets.Blur32
            }}>
                <HeaderSecondary text='Biography' style={{...MarginerPresets._2}}/>
                <TextPrimary text={bio} style={{...MarginerPresets._2, textAlign: TextAlign.Center}}/>

                <MemberBulletSection heading='Education' num={0} data={education}/>
                <MemberBulletSection heading='Past Experience' num={1} data={[experience]}/>
                <MemberBulletSection heading='Ask Me About:' num={2} data={ask}/>
            </Surface>
        </Modal>
        <Surface style={{
            maxWidth: 225,
            ...FlexerPresets.ColumnCenter,
            ...BordererPresets._8,
            ...Shadower({blur: 16, voffset: 4, hoffset: 4, spread: 2, color: 'rgba(0,0,0,.4)'}),
            ...MarginerPresets._4,
            overflow: 'hidden'
        }}>
            <Image style={{maxWidth: 250, userSelect: 'none'}} src={src} alt={`${first_name} ${last_name} profile image.`}/>
            <HeaderSecondary text={first_name}/>
            <HeaderSecondary text={last_name}/>
            <TextPrimaryCenter text={title} style={{maxWidth: '90%', ...MarginerPresets._0}}/>
            <Surface style={Marginer({bottom: 0})}>
                <a href={`https://www.linkedin.com/in/${ln}`} style={{textDecoration: 'none'}}>
                    <TextPrimary
                        text='LinkedIn'
                        style={{color: ColorSet.Secondary4, display: 'inline'}}
                    />
                </a>
                <TextPrimaryCenter text='|' style={{display: 'inline'}}/>
                <Action onClick={() => setOpen(true)}>
                    <TextPrimary
                        text='Bio'
                        style={{color: ColorSet.Secondary4, display: 'inline', cursor: 'pointer'}}
                    />
                </Action>
            </Surface>
        </Surface>
    </>;
}
